    align-items: center;
    border: 0.5px solid #E7E7E7;
    cursor: pointer;
}

.breadcrumb {
    // font-weight: bold;
    font-weight: 400;
    font-size: 13px;
    padding-bottom: 40px;

    .crumbs {
        font-weight: 400;

        &:hover {
            font-weight: 600;
            cursor: pointer;
        }
    }

    .crumbSelected {
        text-decoration: underline;
        font-weight: 600;
    }

}

