.sidebarTitle {
    padding-top: 20px;
    padding-bottom: 20px;
    font-family: 'Montserrat';
    font-style: normal;
    font-weight: 600;
    font-size: 16px;
    line-height: 20px;
    display: flex;
    align-items: center;
    color: #464B4E;
}

.sidebarScroll {
    height: calc(100vh - 330px);
    overflow: scroll;
}

.sidebarCard {
    border: 0.5px solid #E7E7E7;
    padding: 30px;
    font-weight: 500;
    font-size: 15px;
    display: flex;
    align-items: center;
    margin-top: 20px;
    position: relative;

    &:hover {
        background: whitesmoke;
        padding-left: 40px;
        transition: 0.2s all ease;
        cursor: pointer;
    }
}

.sidebarCardDisabled {
    border: 0.5px solid #E7E7E7;
    padding: 30px;
    font-weight: 500;
    font-size: 15px;
    display: flex;
    align-items: center;
    margin-top: 20px;
    opacity: 0.5;
}


