.colourInput {
    display: grid;
    grid-template-columns: 7fr 2fr;
    border: 0.5px solid #e7e7e7;
    font-weight: 700;
    font-size: 16px;
    line-height: 20px;
    height: 62px;

    &>div:first-child {
        display: flex;
        align-items: center;
        padding-left: 20px;
    }
}

.NextBtn {
    background: rgba(75, 157, 220, 1);
    color: white;
    font-weight: 600;
    text-align: center;
    justify-content: center;
    align-items: center;
    border-radius: 25px;
    margin-top: 20px;
    height: 50px;
    display: flex;
    margin: 20px auto;
    width: 90%;
}

.footerBtns {
    background-color: white;
    position: absolute;
    // width: 400px;
    width: 100%;
    height: 70px;
    bottom: 0;
    font-weight: 700;
    font-size: 18px;
    line-height: 24px;

    display: grid;
    grid-template-columns: 1fr 1fr;


    border-top: 0.5px solid #e7e7e7;
    display: flex;
    justify-content: space-between;

    &>div:first-child {
        width: 50%;

        opacity: 0.3;
        border-right: 0.5px solid #e7e7e7;
        display: flex;
        align-items: center;
        justify-content: center;

        &:hover {
            background-color: whitesmoke;
            transition: all 300ms ease-in-out;
            cursor: pointer;
            opacity: 1;
        }
    }

    &>div:not(:first-child) {
        width: 50%;

        border-left: 0.5px solid #e7e7e7;
        display: flex;
        align-items: center;
        justify-content: center;

        &:hover {
            background-color: #182542;
            color: white;
            transition: all 300ms ease-in-out;
            cursor: pointer;
        }
    }
}

.filesUpload {
    padding-top: 20px;
    display: flex;
    height: 170px;
    flex: 0 0 170px;
    justify-content: space-between;
    padding-bottom: 30px;
    margin-bottom: 25px;

    &.last {
        margin-bottom: 30px;
    }

    .fileInp {
        border: 1px solid #e7e7e7;
        position: relative;
        display: flex;
        justify-content: center;
        align-items: center;

        img {
            height: 55%;
            width: auto;
            cursor: pointer;

            &:hover {
                transform: scale(1.03);
            }

            &.true {
                transform: scale(1);
                height: 100%;
                width: 100%;
                object-fit: contain;

                &.white {
                    background: white;
                }
            }
        }

        input {
            display: none;
        }

        .text {
            font-weight: 500;
            position: absolute;
            bottom: -25px;
            font-size: 13px;
            text-align: center;
        }

        .hovTxt {
            display: none;
            font-size: 13px;
        }

        &:hover {
            .hovTxt {
                display: flex;
                position: absolute;
                top: 0;
                bottom: 0;
                left: 0;
                right: 0;
                justify-content: center;
                align-items: center;
                background: #fffa;
                font-weight: bold;
                text-align: center;
            }
        }

        &.icon {
            flex: 0 0 26%;
            max-width: 26%;
        }

        &.cover {
            flex: 0 0 42%;
            max-width: 42%;
        }

        &.fullLogo {
            flex: 0 0 48%;
            max-width: 48%;
        }
    }
}