.back-right,
.close-img {
  border: 0.5px solid #E7E7E7;
  width: 40px;
  height: 40px;
  padding: 10px;
  cursor: pointer;

}

.icon-imgs {
  overflow: hidden;
  position: relative;
  height: 40px;
}

.icon-imgs::after {
  background: #212224;
  content: "";
  height: 200px;
  left: -105px;
  opacity: 1;
  position: absolute;
  top: -70px;
  width: 20px;
  transition: all .55s cubic-bezier(.19, 1, .22, 1);
  -webkit-transform: rotate(35deg);
  transform: rotate(35deg);
  z-index: -10;
}

.icon-imgs:hover::after {
  left: 155%;
  transition: all .55s cubic-bezier(.19, 1, .22, 1);
  z-index: 10;
}

.icons-sty {
  margin-left: auto;
  margin-top: 8px;
  display: flex;
}

.title-text {
  font-weight: 600;
  font-size: 16px;
  color: #100F0D;
}

.user-title {
  margin-top: 50px;
}

.usertyp-border {
  border: 0.5px solid #E7E7E7;
  display: flex;
  height: 83px;
  align-items: center;
  cursor: pointer;
  position: relative;
  overflow: hidden;
}

