.colourInput {
  display: grid;
  grid-template-columns: 7fr 2fr;
  border: 0.5px solid #e7e7e7;
  font-weight: 700;
  font-size: 16px;
  line-height: 20px;
  height: 62px;

  &>div:first-child {
    display: flex;
    align-items: center;
    padding-left: 20px;
  }
}


.footerBtns {
  background-color: white;
  position: absolute;
  // width: 400px;
  width: 100%;
  height: 70px;
  bottom: 0;
  font-weight: 700;
  font-size: 18px;
  line-height: 24px;

  display: grid;
  grid-template-columns: 1fr 1fr;


  border-top: 0.5px solid #e7e7e7;
  display: flex;
  justify-content: space-between;

  &>div:first-child {
    width: 50%;

    opacity: 0.3;
    border-right: 0.5px solid #e7e7e7;
    display: flex;
    align-items: center;
    justify-content: center;

    &:hover {
      background-color: whitesmoke;
      transition: all 300ms ease-in-out;
      cursor: pointer;
      opacity: 1;
    }
  }

  &>div:not(:first-child) {
    width: 50%;

    border-left: 0.5px solid #e7e7e7;
    display: flex;
    align-items: center;
    justify-content: center;

    &:hover {
      background-color: #182542;
      color: white;
      transition: all 300ms ease-in-out;
      cursor: pointer;
    }
  }
}

.btnBack {
  background: rgba(241, 241, 241, 0.5);
  border-radius: 35px;
