  padding: 10px;
  text-align: center;
  color: #FFFFFF;
  font-size: 20px;
  margin: auto;
  cursor: pointer;

  &:disabled {
    opacity: 0.6;
  }

  &:hover {
    text-shadow: 2px 3px 4px rgba(255, 255, 255, 0.589);
  }
}



.filesUpload {
  padding-top: 20px;
  display: flex;
  height: 170px;
  flex: 0 0 170px;
  justify-content: space-between;
  padding-bottom: 30px;
  margin-bottom: 25px;

  &.last {
    margin-bottom: 30px;
  }

  .fileInp {
    border: 1px solid #e7e7e7;
    position: relative;
    display: flex;
    justify-content: center;
    align-items: center;

    img {
      height: 55%;
      width: auto;
      cursor: pointer;

      &:hover {
        transform: scale(1.03);
      }

      &.true {
        transform: scale(1);
        height: 100%;
        width: 100%;
        object-fit: contain;

        &.white {
          background: white;
        }
      }
    }

    input {
      display: none;
    }

    .text {
      font-weight: 500;
      position: absolute;
      bottom: -25px;
      font-size: 13px;
      text-align: center;
    }

    .hovTxt {
      display: none;
      font-size: 13px;
    }

    &:hover {
      .hovTxt {
        display: flex;
        position: absolute;
        top: 0;
        bottom: 0;
        left: 0;
        right: 0;
        justify-content: center;
        align-items: center;
        background: #fffa;
        font-weight: bold;
        text-align: center;
      }
    }

    &.icon {
      flex: 0 0 26%;
      max-width: 26%;
    }

    &.cover {
      flex: 0 0 42%;
      max-width: 42%;
    }

    &.fullLogo {
      flex: 0 0 48%;
      max-width: 48%;
    }
  }
}

@media only screen and (max-width: 600px) {
  .right-drawer-hidden {
    position: absolute;
    background: white;
    top: 123px;
    right: 0;
    overflow: hidden;
    width: 100vw;
    border-left: 1px solid #e5e5e5;
    margin-top: -1px;
    transition: transform 0.5s cubic-bezier(0.4, 0, 0.2, 1);
    background: white;
    z-index: 1;
    transform: TranslateX(100%);
  }

  .right-drawer-visible {
    position: absolute;
    background: white;
    top: 123px;
    right: 0;
    overflow: hidden;
    width: 100vw;
    border-left: 1px solid #e5e5e5;
    margin-top: -1px;
    transition: transform 0.5s cubic-bezier(0.4, 0, 0.2, 1);
    background: white;
    z-index: 10;
    transform: TranslateX(0%);
  }
}